.landing {
  position: relative; /* Ensure relative positioning for pseudo-element */
  background-color: black !important;
  height: 100vh;
  padding-top: 130px;
}

.landing::before {
  content: '';
  position: absolute;
  top: -50%; /* Position shine above the container */
  left: -50%; /* Position shine to the left of the container */
  width: 100%; /* Cover the entire container */
  height: 200%; /* Cover the entire container */
  border-radius: 0 0 20px 20px;
  background: radial-gradient(circle, rgba(255, 157, 0, 0.993) 2%, transparent 60%); /* Golden shine */
  -webkit-animation: shine 3s ;
          animation: shine 3s ; /* Shine animation */
}

@-webkit-keyframes shine {

  50% {
    -webkit-transform: translate(-50%, -50%) scale(1.2);
            transform: translate(-50%, -50%) scale(1.2); /* Scale up */
  }

}

@keyframes shine {

  50% {
    -webkit-transform: translate(-50%, -50%) scale(1.2);
            transform: translate(-50%, -50%) scale(1.2); /* Scale up */
  }

}


